
  .container {
    max-width: 1000px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 10px;
    padding-right: 10px;
  }
  
  h2 {
    font-size: 26px;
    margin: 20px 0;
    text-align: center;
    small {
      font-size: 0.5em;
    }
  }
  
  .responsive_table {
    li {
      border-radius: 3px;
      padding: 25px 30px;
      display: flex;
      justify-content: space-between;
      margin-bottom: 25px;
    }
    .table_header {
      background-color: #95A5A6;
      font-size: 14px;
      text-transform: uppercase;
      letter-spacing: 0.03em;
    }
    .table_row {
      background-color: #ffffff;
      box-shadow: 0px 0px 9px 0px rgba(0,0,0,0.1);
    }
    .col_1 {
      flex-basis: 10%;
    }
    .col_2 {
      flex-basis: 10%;
    }
    .col_3 {
      flex-basis: 10%;
    }
    .col_4 {
      flex-basis: 10%;
    }
    .col_5 {
        flex-basis: 10%;
      }
      .col_6 {
        flex-basis: 10%;
      }
      .col_7 {
        flex-basis: 10%;
      }
      .col_8 {
        flex-basis: 10%;
      }
      .col_9 {
        flex-basis: 10%;
      }
    
    @media all and (max-width: 767px) {
      .table_header {
        display: none;
      }
      .table_row{
        
      }
      li {
        display: block;
      }
      .col {
        
        flex-basis: 100%;
        
      }
      .col {
        display: flex;
        padding: 10px 0;
        &:before {
          color: #6C7A89;
          padding-right: 10px;
          content: attr(data-label);
          flex-basis: 50%;
          text-align: right;
        }
      }
    }
  }